
























import { Prop, Component, Vue } from 'vue-property-decorator'

@Component
export default class ModalInfo extends Vue {
  @Prop({ default: 'modal-info' }) private id!: string
  @Prop() private infoMess!: string

  hideModal() {
    this.$bvModal.hide(this.id)
    setTimeout(() => {
      this.$emit('close')
      clearTimeout()
    }, 300)
  }
}
